<div class="char-panel"><p class="title-font-style text-center" :style="{color: color.colorDark}">{{ title }}</p><div class="d-flex flex-wrap"><div class="char-block" v-for="person in member" :class="color.label==&quot;grey&quot;?&quot;&quot;:&quot;w-1-2&quot;"><p class="font-style" :style="{color: color.colorDark}"><i class="bi bi-person-vcard-fill icon-style"></i>{{ person.label }}</p><p class="desc-font-style">{{ person.desc }}</p></div></div></div>